<template>
    <div style="padding:0.2rem">
        <van-field v-model="password" type="password" placeholder="请输入登录密码" clearable label-width="3.5rem"/>
        <van-field v-model="password_comfirm" type="password" placeholder="请再次输入登录密码" clearable label-width="3.5rem"/>
        <van-row style="margin-top:1rem">
            <van-col span="6"></van-col>
            <van-col span="6" align="center">
                <van-button class="button" round size="small" @click="clear">清空</van-button>
            </van-col>
            <van-col span="6" align="center">
                <van-button class="button" round size="small" type="danger" @click="submit">提交</van-button>
            </van-col>
            <van-col span="6"></van-col>
        </van-row>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    name:'RevisePassword',
    data(){
        return {
            password:"",
            password_comfirm:""
        }
    },
    methods:{
        clear(){
            this.password="";
            this.password_comfirm="";
        },
        submit(){
            if(this.password_comfirm.length==0||this.password.length==0){
                Toast.fail("填写密码不得为空");
            }else if(this.password!=this.password_comfirm){
                Toast.fail("填写密码不一致");
            }else{
                android.showLodingDialog("正在修改账号密码...");
                this.$api.get("/revisePassword",{staffID:android.getUserID(),staffPassword:this.password},response=>{
                    android.closeLodingDialog();
                    if (response.status >= 200 && response.status < 300) {
                        android.toastLog(response.data.msg);
                        android.back();
                    } else {
                        Toast.fail('重置密码出现异常');
                    }
                });
            }
        }
    },
    computed:{

    }
}
</script>


