<template>
    <Div style="padding:0.2rem">
        <van-search
            v-model="number"
            placeholder="请输入工序流转卡编码"
            show-action
            shape="round"
            clear
            @search="onSearch"
            style="margin-right:1rem"
            right-icon
            >
            <div slot="action" disabled="loading" @click="onSearch">搜索</div>
            <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix='icon' name="saomiao" /></div>
        </van-search>
        <van-cell-group>
            <van-field
                v-model="transferCardNumber"
                label="流转卡编码："
                disabled
                label-align="left"
            />
            <van-field
                v-model="orderNumber"
                label="订单号："
                disabled
                label-align="left"
            />
            <van-field
                readonly
                disabled
                label="工序号："
                label-align="left"
                :value="processNumber"
            />    
            <van-field
                v-model="inputNumber"
                required
                :disabled="flag"
                label="投入数："
                label-align="left"
                type="number"
            />
            <van-field
                v-model="outputNumber"
                required
                :disabled="flag"
                label="产出数："
                label-align="left"
                type="number"
            />
            <van-field
                v-model="badNumber"
                label="不良数："
                label-align="left"
                disabled
                type="number"
            />
            <van-field
                v-model="taskTime"
                disabled
                label="工时："
                label-align="left"
                type="number"
            />
            <van-field
                readonly
                clickable
                label="不良项目："
                label-align="left"
                :value="badProject"
                placeholder="选择不良项目"
                @click="showBadPicker=true"
                v-if="flag==false&&badProjectshow==true"
            />
            <van-field
                readonly
                clickable
                label="不良项目："
                label-align="left"
                :value="badProject"
                placeholder="选择不良项目"
                v-else
            />
            <van-popup v-model="showBadPicker" position="bottom">
                <van-picker
                    show-toolbar
                    :columns="bads"
                    @cancel="showBadPicker = false"
                    @confirm="onBadConfirm"
                />
            </van-popup>  
        </van-cell-group>
        <div style="margin-top:1rem">
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="clear">清空</van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="submit" :disabled="flag">提交</van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </Div>
</template>
<script>
import Vue from 'vue';
import { Loading } from 'vant';
import { Overlay } from 'vant';
import { Dialog } from 'vant';
import { Icon } from 'vant';
import { Toast } from 'vant';
Vue.use(Overlay);
Vue.use(Loading);
export default {
    name:'JobBooking',
    data(){
        return {
            number:'',
            scannerNumber:'',
            transferCardNumber:'',
            flag:true,
            processNumber:'',
            processID:'',
            orderNumber:'',
            inputNumber:'',
            outputNumber:'',
            badID:'',
            badProject:'',
            showProcessPicker: false,
            showBadPicker:false,
            unitTime:1,
            processes: [{"processID":"12","text":"工序1"}, {"processID":"13","text":"工序2"},{"processID":"14","text":"工序3"}, 
                {"processID":"15","text":"工序4"}, {"processID":"16","text":"工序5"}],
            bads: [{"BadID":"12","text":"工序1"}, {"BadID":"13","text":"工序2"},{"BadID":"14","text":"工序3"}, 
                {"BadID":"15","text":"工序4"}, {"BadID":"16","text":"工序5"}]
        }
    },
    components:{
    },
    created(){
        this.getAllBadDes();
    },
    mounted(){
        window.scannerSearch=this.scannerSearch;
    },
    computed:{
        badNumber(){
            if((this.inputNumber-this.outputNumber).toFixed(0)<=0){
                this.badProject='';
                this.badID='';
            }
            return (this.inputNumber-this.outputNumber).toFixed(0);
        },
        taskTime(){
            return (this.outputNumber/this.unitTime).toFixed(2);
        },
        badProjectshow(){
            return this.badNumber>0;
        }
    },
    watch:{
        scannerNumber:{
            deep: true,
            handler: function (newVal,oldVal){
                this.number=newVal;
                this.onSearch();
            }
        }
    },
    methods:{
        onSearch:function(){
            android.showLodingDialog("正在获取工序流转卡数据...");
            this.$api.get("/selectProcessBooking",{Number:this.number},response=>{
                android.closeLodingDialog();
                this.clear();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.msg.transferCardNumber.length==0){
                        Toast.fail('工序流转单不存在,请确认工序流转卡编码');
                    }else if(response.data.msg.processes.length==0){
                        Toast.fail('当前工单已报工完毕');
                    }else{
                        Toast.success('获取工序流转单成功');
                        this.transferCardNumber=response.data.msg.transferCardNumber;
                        this.orderNumber=response.data.msg.orderNumber;
                        this.onProcessConfirm(response.data.msg.processes);
                        this.flag=false;
                    }
                } else {
                    Toast.fail('获取工序流转单时出现异常，请重新操作');
                }
            });
        },
        onProcessConfirm(value) {
            this.processNumber = value.text;
            this.processID=value.processID;
            this.unitTime=value.unitTime;
        },
        onBadConfirm(value) {
            this.badID = value.badID;
            this.badProject=value.text;
            this.showBadPicker = false;
        },
        clear(){
            this.transferCardNumber='',
            this.processNumber='',
            this.processID='',
            this.inputNumber='',
            this.outputNumber='',
            this.badProject='',
            this.badID=''
        },
        submit(){
            if(this.badNumber<0){
                Toast.fail("产出数量不得大于投入数量");
                return;
            }
            if(this.badNumber>0&&this.badID==''){
                Toast.fail("存在不良，请填写不良项目");
                return;
            }
            if(this.processID.length>0&&this.inputNumber.length>0&&this.outputNumber.length>0&&this.taskTime.length>0){
                Dialog.confirm({
                    title: '确认',
                    message: '请确认当前数据已填写正确'
                    }).then(() => {
                        android.showLodingDialog("正在提交工序流转卡数据...");
                        var submitData={
                            transferCardNumber:this.transferCardNumber,
                            processID:this.processID,
                            processNumber:this.processNumber,
                            inputNumber:this.inputNumber,
                            outputNumber:this.outputNumber,
                            taskTime:this.taskTime,
                            badID:this.badID,
                            badNumber:this.badNumber,
                            badProject:this.badProject,
                            userID:android.getUserID(),
                            orderNumber:this.orderNumber
                        };
                        this.$api.get("/submitProcessBooking",{submitData:submitData},response=>{
                            android.closeLodingDialog();
                            if (response.status >= 200 && response.status < 300) {
                                if(response.data.success=='true'){
                                    Dialog.alert({
                                        message: response.data.msg
                                        }).then(() => {
                                        android.back();
                                    });
                                }else{
                                    Toast.fail(response.data.msg);
                                }
                            } else {
                                Toast.fail("数据提交出现异常,请重新提交");
                            }
                        });
                    }).catch(() => {
                        return;
                });
            }else{
                Toast.fail("请填写必填项");
            }
        },
        getAllBadDes(){
            android.showLodingDialog("正在加载基础数据...");
            this.$api.get("/selectAllBadDes",{},response=>{
                android.closeLodingDialog();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=='true'){
                        this.bads=response.data.msg;
                    }else{
                        android.alertDialog("警告",'获取不良项目出现异常，请重新打开');
                    }
                } else {
                    android.alertDialog("警告",'获取不良项目出现异常，请重新打开');
                }
            });
        },
        openScanner(){
            android.openScanner();
        },
        scannerSearch(data){
            this.scannerNumber=data;
        }
    }
}
</script>
<style scoped>
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.block {
  width: 3rem;
  height: 3rem;
}
</style>


